@import url('https://fonts.googleapis.com/css2?family=Caprasimo&display=swap%27');
@import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@700&display=swap');

.welcome {
    text-align: center;
    font-weight: bold;
    font-size: 50px;
    margin-top: 100px;
    color: var(--border);
}

.title {
    text-align: center;
    font-weight: bolder;
    font-size: 100px;
    color: var(--border);
}

.connta {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.main {
    background-color: rgba(132, 126, 126, 0.5);
    position: relative;
    top: 100px;
}

.logoimg {
    width: 300px;
    margin-top: 10px;
    border-radius: 20px;
}

.dark-theam {
    --border: white;
    --background: rgb(18, 14, 36);
}

.light-theam {
    --border: rgb(0, 0, 0);
    --background: white;
}

.section {
    background-color: var(--background);
}

.home-section {
    background-image: url("../Images/carsoul1.jpeg");
    min-height: 646px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.homeh1 {
    font-weight: bolder;
    text-align: left;
    font-size: xx-large;
}

.content-1 {
    margin-top: 20px;
    display: flex;
    background-image: url("../Images/about.jpg");
    flex-direction: row;
    padding-left: 20px;
    background-position: center;
    background-repeat: no-repeat;
}

.content-2 {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    background: url("../Images/contactus.jpg") no-repeat;
    background-position: center;
    background-size: cover;
    -webkit-backdrop-filter: blur(50%);
    backdrop-filter: blur(50%);
}

.h5 {
    font-size: 25px;
    margin-top: 30px;
    font-family: 'Alkatra', 'sans-serif';
}

.maincontent {
    width: 100%;
    align-self: center;
    display: flex;
    flex-direction: column;
}

.homeimage {
    width: 100%;
    margin-left: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 30px;
}

.conclude {
    text-align: center;
    font-weight: bold;
}

.home_name {
    background-color: #ccc9f2;
    color: black;
    width: 400px;
    height: 40px;
}

.home_email {
    background-color: #ccc9f2;
    color: black;
    margin-top: 20px;
    width: 400px;
    height: 40px;
}

.home_message {
    background-color: #ccc9f2;
    color: black;
    margin-top: 20px;
    width: 400px;
    width: 40%;
    border: none;
    outline: none;
    border-bottom: 2px solid rgb(0, 0, 0);
    color: rgb(0, 0, 0);
    background: none;
    transition: all .5s ease-in-out;
}

.home_name::placeholder {
    color: black;
}

.home_email::placeholder {
    color: black;
}

.home_message::placeholder {
    color: black;
}

/* 6369f5 */
.Send {
    width: 40%;
    color: rgb(0, 0, 0);
    font-weight: bold;
    height: 30px;
    font-size: larger;
}

.contactimage {
    border-radius: 25px;
}

.devimage {
    height: 500px;
    width: 350px;
    border-radius: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.footer {
    background-color: black;
    width: 100%;
    text-align: right;
    margin-top: 10px;
    font-weight: bold;
    font-size: large;
    color: white;
}

.input-box-home .input-home {
    width: 40%;
    border: none;
    outline: none;
    border-bottom: 2px solid rgb(0, 0, 0);
    color: rgb(0, 0, 0);
    background: none;
    transition: all .5s ease-in-out;
}

.input-home {
    height: 50px;
}

.input-box-home .input-home:hover,
.input-box-home .home_message:hover .input-home:focus {
    width: 50%;
}


.buttion-home .button-home {
    width: 40%;
    background-color: #2196F3;
    border: none;
    color: white;
    padding: 8px 18px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    border-radius: 12px;
}

.buttion-home .button-home:hover {
    background-color: #008cff;
    cursor: pointer;
}